.application-container {
    display: flex;
    margin: 0 auto;
    height: 100dvh;
    justify-content: center;
    align-items: center;
}

.assembly-container,
.binary-container {
    display: flex;
    margin: 0 auto;
    height: 70%;
    border: 1px solid black;
    width: 40%;
}

.assemble-button-container {
    display: flex;
    margin: 0 auto;
    height: 70%;
    width: 5%;
    padding: 10px;
    align-items: center;
    justify-content: center;
}

.cm-editor {
  outline: none !important;
  width: 100%;
}


